/* Background */
.sch_container {
  display: flex;
  height: 100vh;
  background: linear-gradient(135deg, #FAFAFA, #8B5E3C);
  overflow: hidden;
  flex-wrap: wrap;
}

/* Sidebar */
.sch_sidebar {
  width: 25%;
  background: rgba(61, 44, 30, 0.8);
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 3px solid #DFAF2B;
  box-shadow: 3px 0 15px rgba(0, 0, 0, 0.2);
}

/* Glassmorphism Filter Box */
.sch_filter {
  width: 100%;
  padding: 20px;
  border-radius: 15px;
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Inputs */
.sch_input {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  font-size: 14px;
  background: rgba(255, 255, 255, 0.3);
  color: #3D2C1E;
  transition: 0.3s ease-in-out;
  outline: none;
}

.sch_input:focus {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 10px rgba(139, 94, 60, 0.4);
}

/* Buttons */
.sch_btn {
  background: #DFAF2B;
  color: white;
  padding: 12px;
  font-size: 16px;
  border-radius: 8px;
  width: 100%;
  transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
  border: none;
  cursor: pointer;
}

.sch_btn:hover {
  background: #3D2C1E;
  transform: scale(1.05);
}

/* Dog Grid */
.sch_dog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
}

/* Pagination Buttons */
.sch_pagination {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.sch_page-btn {
  background: rgba(255, 255, 255, 0.2);
  color: #DFAF2B;
  padding: 12px 20px;
  border: 2px solid #DFAF2B;
  border-radius: 10px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.sch_page-btn:hover {
  background: #DFAF2B;
  color: white;
  transform: scale(1.1);
}

.sch_page-btn:disabled {
  background: rgba(255, 255, 255, 0.1);
  color: #8B5E3C;
  border: 2px solid #8B5E3C;
  cursor: not-allowed;
}

/* Responsive Sidebar */
@media (max-width: 1024px) {
  .sch_sidebar {
    width: 100%;
    text-align: center;
    border-right: none;
    border-bottom: 2px solid #DFAF2B;
    padding-bottom: 15px;
  }

  .sch_main {
    width: 100%;
  }
}

/* Responsive Filters */
@media (max-width: 768px) {
  .sch_filter {
    width: 90%;
    margin: 0 auto;
    padding: 15px;
  }

  .sch_input {
    font-size: 14px;
    padding: 10px;
  }

  .sch_page-btn {
    font-size: 14px;
    padding: 10px 15px;
  }
}

@media (max-width: 576px) {
  .sch_dog-grid {
    grid-template-columns: repeat(1, 1fr);
  }

  .sch_filter {
    width: 100%;
  }
}
