/* Full Page Background */
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #FAFAFA, #8B5E3C);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Glassmorphism Effect */
.glass {
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

/* Login Container */
.login-container {
  max-width: 900px;
  width: 90%;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  animation: fadeIn 1.2s ease-in-out;
  padding: 40px;
}

/* Left Section (Login Info) */
.login-left {
  width: 50%;
  background: linear-gradient(135deg, #3D2C1E, #8B5E3C);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 40px;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  animation: slideIn 1s ease-in-out;
}

.subtitle {
  font-size: 1.8rem;
  font-weight: 600;
  color: #DFAF2B;
}

/* Right Section (Login Form) */
.login-right {
  width: 50%;
  padding: 40px;
  background: rgba(255, 255, 255, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Form Wrapper */
.form-box {
  width: 100%;
  padding: 20px;
  border-radius: 15px;
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Inputs */
.input {
  width: 100%;
  padding: 14px;
  border-radius: 10px;
  border: none;
  font-size: 16px;
  background: rgba(255, 255, 255, 0.3);
  color: #3D2C1E;
  transition: 0.3s ease-in-out;
  outline: none;
}

.input:focus {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 10px rgba(139, 94, 60, 0.4);
}

/* Button */
.btn-primary {
  background: #DFAF2B;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.btn-primary:hover {
  background: #8B5E3C;
  transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .login-container {
    flex-direction: column;
    text-align: center;
  }

  .login-left, .login-right {
    width: 100%;
    padding: 30px;
  }
}

@media (max-width: 768px) {
  .form-box {
    width: 100%;
    padding: 15px;
  }

  .input {
    font-size: 14px;
    padding: 12px;
  }

  .btn-primary {
    font-size: 16px;
    padding: 10px 15px;
  }
}

@media (max-width: 576px) {
  .login-container {
    width: 95%;
  }

  .title {
    font-size: 2rem;
  }

  .subtitle {
    font-size: 1.5rem;
  }

  .btn-primary {
    font-size: 14px;
    padding: 10px 12px;
  }
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
  from { opacity: 0; transform: translateX(-30px); }
  to { opacity: 1; transform: translateX(0); }
}
